.profile__title {
	max-width: 294px;
	margin: 0;
	font-weight: 500;
	font-size: 42px;
	line-height: 48px;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

@media screen and (max-width: 875px) {
	.profile__title {
		margin-bottom: 8px;
		margin-right: 15px;
	}
}

@media screen and (max-width: 394px) {
	.profile__title {
		max-width: 189px;
		font-size: 27px;
		line-height: 33px;
		align-self: center;
	}
}
